import React, { Component } from 'react';
import axios from 'axios';
import CardItem from '../card';
import ReactJWPlayer from 'react-jw-player';
import './styleList.css';

// import { AuthUserContext, withAuthorization } from '../Session';

class CardList extends Component {
	constructor(props) {
		super(props);
		this.state = {
			playlist: [],
			urls: [],
			title: '',
			description: '',
		};
	}
	componentDidMount() {
		axios.get('https://cdn.jwplayer.com/v2/playlists/SEw1rfH9').then(res => {
			console.log(res.data);
			this.setState({
				playlist: res.data.playlist,
				title: res.data.title,
				description: res.data.description,
			});
		});
	}

	render() {
		return (
			<div className="m-5 fondo-eventos">
				<p className="display-4 mt-5 d-flex justify-content-center text-white">
					{this.state.title}
				</p>
				<p>{this.state.description}</p>
				<div className="d-flex flex-wrap justify-content-center">
					<div className="card-columns">
						{this.state.playlist.map(e => (
							<div className="card my-3 mx-4" style={{ width: '22rem' }}>
								<div className="card-img-top">
									<ReactJWPlayer
										playerId={e.title}
										playerScript="https://cdn.jwplayer.com/libraries/LUykEJtT.js"
										file={e.sources[0].file}
										image={e.images[3].src}
									/>
								</div>
								<div className="card-body">
									<h5 className="card-title">{e.title}</h5>
									<p className="card-text">{e.description}</p>
									<a href="" className="btn btn-secondary">
										Ver Mas
									</a>
								</div>
							</div>
						))}
					</div>
				</div>
			</div>
		);
	}
}

// const condition = authUser => !!authUser;
// export default withAuthorization(condition)(CardList);
export default CardList;
