import React, { Component } from 'react';
import ReactJWPlayer from 'react-jw-player';

class CardItem extends Component {
	constructor(props) {
		super(props);
		this.state = {};
	}
	render() {
		const { url, song, artist, festival, description } = this.props;
		return (
			<React.Fragment>
				<div className="card m-3">
					<div className="card-header">Festival: <strong>{festival}</strong> </div>
                    <ReactJWPlayer>
                        
                    </ReactJWPlayer>
					<video className="card-img-top" controls>
						<source src={url} type="video/mp4" />
					</video>
					<div className="card-body">
						<h5 class="card-title">Canción: {song}</h5>
						<p class="card-subtitle mb-2 text-muted">Artista: {artist}</p>
						<div class="card-text">Descripción: {description}</div>
					</div>
				</div>
			</React.Fragment>
		);
	}
}

export default CardItem;
