import React, { Component } from 'react'
import {withFirebase} from '../Firebase'

class Signout extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }
    render() { 
        return ( <div>
            <button type="button" className="btn btn-outline-secondary" onClick={this.props.firebase.doSignOut}>
                Cerrar Sesión
            </button>
        </div> );
    }
}
 
export default withFirebase(Signout);