import React from 'react';
import { Link } from 'react-router-dom';
import * as ROUTES from '../../constans/routes';
import logo from '../../assets/logo_blanco.png';

import Signout from '../SignOut';
import { AuthUserContext } from '../Session';
// import { auth } from 'firebase';
import './style.css';

const Navigation = () => (
	<div>
		<AuthUserContext.Consumer>
			{authUser => (authUser ? <NavigationAuth /> : <NavigationNonAuth />)}
		</AuthUserContext.Consumer>
	</div>
);

const NavigationAuth = () => (
	<React.Fragment>
		<nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark navbar-toggleable-sm ">
			<Link to={ROUTES.LANDING} className="navbar-brand">
				StreamTOK
			</Link>
			<button
				className="navbar-toggler ml-auto"
				type="button"
				data-toggle="collapse"
				data-target="#navbarSupportedContent"
				aria-controls="navbarSupportedContent"
				aria-expanded="false"
				aria-label="Toggle navigation"
			>
				<span className="navbar-toggler-icon"></span>
			</button>

			<div className="collapse navbar-collapse" id="navbarSupportedContent">
				<div className="navbar-nav mr-auto ml-auto text-center">
					<Link className="nav-item nav-link" to={ROUTES.HOME}>
						Home
					</Link>
					<Link className="nav-item nav-link" to={ROUTES.ACCOUNT}>
						Account
					</Link>
					<Link className="nav-item nav-link" to={ROUTES.CARD_LIST}>
						Eventos
					</Link>
				</div>
				<div className="d-flex flex-row justify-content-center">
					<a href="/" className="btn btn-outline-primary mx-2">
						F
					</a>
					<a href="/" className="btn btn-outline-danger mx-2">
						I
					</a>
					<Signout />
				</div>
			</div>
		</nav>
	</React.Fragment>
);

const NavigationNonAuth = () => (
	<div>
		{/* <section
			style={{ height: '10vh' }}
			className="container-fluid px-0 titulo bg-dark d-flex justify-content-center ml-auto mx-auto"
		>
			<Link to={ROUTES.LANDING} className="navbar-brand text-white">
				StreamTOK
			</Link>
		</section> */}
		<nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-dark navbar-toggleable-sm">
			<Link to={ROUTES.LANDING} className="navbar-brand">
				StreamTOK
			</Link>
			<button
				className="navbar-toggler ml-auto"
				type="button"
				data-toggle="collapse"
				data-target="#navbarSupportedContent"
				aria-controls="navbarSupportedContent"
				aria-expanded="false"
				aria-label="Toggle navigation"
			>
				<span className="navbar-toggler-icon"></span>
			</button>

			<div className="collapse navbar-collapse" id="navbarSupportedContent">
				<div className="navbar-nav mr-auto ml-auto text-center">
					<Link className="nav-item nav-link" to={ROUTES.LANDING}>
						Inicio
					</Link>

					<Link className="nav-item nav-link" to={ROUTES.CARD_LIST}>
						Eventos
					</Link>
					<Link className="nav-item nav-link" to="/">
						Canales
					</Link>
					<a href="/" className="btn btn-outline-primary mx-2">
						F
					</a>
					<a href="/" className="btn btn-outline-danger mx-2">
						I
					</a>
				</div>
				{/* <div className="d-flex flex-row justify-content-center">
					<a href="/" className="btn btn-outline-primary mx-2">
						F
					</a>
					<a href="/" className="btn btn-outline-danger mx-2">
						I
					</a>
					<Link type="button" className="btn btn-outline-secondary" to={ROUTES.SIGN_IN}>
						Iniciar Sesión
					</Link>
				</div> */}
			</div>
			<div></div>
		</nav>
	</div>
);

export default Navigation;
