import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { compose } from 'recompose';

import { SignUpLink } from '../SignUp';
import { PasswordForgetLink } from '../PasswordForget';
import { withFirebase } from '../Firebase';
import * as ROUTES from '../../constans/routes';

const SignIn = () => (
	<div className="d-flex flex-column justify-content-center align-items-center" >
		<div className="card" style={{width:'25rem', marginTop:'5rem'}}>
			<div className="card-body">
				<h5 className="card-title">Iniciar sesión</h5>
				<SignInForm />
			</div>
		</div>
		{/* <h4 className="text-center">Iniciar sesión</h4>
		<SignInForm /> */}
		<PasswordForgetLink />
		<SignUpLink />
	</div>
);

class SigninFormBase extends Component {
	constructor(props) {
		super(props);
		this.state = {
			email: '',
			password: '',
			error: null,
		};
	}
	onSubmit = event => {
		const { email, password } = this.state;
		this.props.firebase
			.doSignInWithEmailAndPassword(email, password)
			.then(() => {
				this.setState({ ...this.state });
				this.props.history.push(ROUTES.HOME);
			})
			.catch(error => {
				this.setState({ error });
			});
		event.preventDefault();
	};
	onChange = event => {
		this.setState({ [event.target.name]: event.target.value });
	};
	render() {
		const { email, password, error } = this.state;

		const isInvalid = password === '' || email === '';
		return (
			<div className="" >
				<form onSubmit={this.onSubmit}>
					<div className="form-group">
						<label forHtml="exampleInputEmail1">E-mail:</label>
						<input
							name="email"
							value={email}
							onChange={this.onChange}
							type="text"
							placeholder="Ingrese aquí su email"
							aria-describedby="emailHelp"
							className="form-control"
						/>
						<small id="emailHelp" class="form-text text-muted"></small>
					</div>
					<div className="form-group">
						<label forHtml="exampleInputEmail1">Contraseña:</label>
						<input
							name="password"
							value={password}
							onChange={this.onChange}
							type="password"
							placeholder="Ingrese aquí su contraseña"
							className="form-control"
						/>
						<small id="emailHelp" class="form-text text-muted"></small>
					</div>
					<button disabled={isInvalid} type="submit" class="btn btn-primary d-blok">
						Ingresar
					</button>
					{error && <p>{error.message}</p>}
				</form>
			</div>

			// <form onSubmit={this.onSubmit}>
			// 	<div class="form-group">
			// 		<label for="exampleInputEmail1">Email address</label>
			// 		<input
			// 			name="email"
			// 			value={email}
			// 			onChange={this.onChange}
			// 			type="text"
			// 			placeholder="Email Address"
			// 			aria-describedby="emailHelp"
			// 		/>
			// 		<small id="emailHelp" class="form-text text-muted">
			// 			We'll never share your email with anyone else.
			// 		</small>
			// 	</div>

			// 	<input
			// 		name="password"
			// 		value={password}
			// 		onChange={this.onChange}
			// 		type="password"
			// 		placeholder="Password"
			// 	/>
			// 	<button disabled={isInvalid} type="submit">
			// 		Sign In
			// 	</button>
			// 	{error && <p>{error.message}</p>}
			// </form>
		);
	}
}

const SignInForm = compose(withRouter, withFirebase)(SigninFormBase);

export default SignIn;

export { SignInForm };
