import React, { Component } from 'react';
import './style.css';
// import video from '../../assets/stream.mp4';
import * as ROUTES from '../../constans/routes';
import { Link } from 'react-router-dom';

class Landing extends Component {
	constructor(props) {
		super(props);
		this.state = {
			openTab: 'Festivales',
		};
	}
	tabs = ['Festivales', 'Recitales', 'Shows'];
	getTabs = () => {
		return this.tabs.map(tab => {
			return (
				<button
					className={`buttonTab ${tab !== this.state.openTab ? 'no-active' : 'active'}`}
					onClick={() => this.handleTabs(tab)}
				>
					{tab}
				</button>
			);
		});
	};
	handleTabs = tab => {
		this.setState({ openTab: tab });
	};

	render() {
		return (
			<div>
				<section className="header content">
					<div className="header-video">
						<video muted autoPlay>
							<source src="https://streamtok.nyc3.digitaloceanspaces.com/PUBLICIDADES/Reel_%20Streamtok_.mp4" />
						</video>
					</div>
					<div className="header-overlay"></div>
					<div className="header-content">
						<h1>StreamTOK</h1>
						<p>
							Lorem ipsum dolor sit amet consectetur adipisicing elit. Odit delectus
							voluptatem, laudantium voluptas dolorum cumque iste.
						</p>
						<Link className="btn" to={ROUTES.CARD_LIST}>Eventos</Link>
					</div>
				</section>

				<section className="publicidad text-white d-flex justify-content-center align-items-center">
					<div className="content-publicidad d-flex justify-content-center align-items-center">Publicite aquí</div>
				</section>

				<section className="quienes-somos container">
					<h4 className="tituloTabs text-center pt-3">
						Festivales, Shows y más...
					</h4>
					<div className="d-flex py-3 d-flex justify-content-center cont-tabs">
						{this.getTabs()}
					</div>
					<div>
						{this.state.openTab === 'Festivales' ? (
							<div className="row p-4">
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Cosquin/INDIO%20LUCUIO%20ROJAS.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Festival%20de%20la%20Avicultura/Los%20Manseros.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Festival%20de%20la%20Avicultura/Abel%20Pintos%20.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Cosquin/Chaque%C3%B1o%20Palavecino.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
							</div>
						) : this.state.openTab === 'Recitales' ? (
							<div className="row p-4">
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Cosquin/Abel%20Pintos%202.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/ELLA%20-%20LA%20%20MONA.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/AIRBAG.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/Los%20Tipitos..jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
							</div>
						) : (
							<div className="row p-4">
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/beatriz%20sarlo.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/gusti%20bancor.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/toman%20abraham.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
								<div className="col-12 col-md-3 mx-auto my-2">
									<img
										src="https://streamtok.nyc3.digitaloceanspaces.com/Eventos/INDIOS.jpg"
										alt="Festival"
										className="img-fluid"
									/>
								</div>
							</div>
						)}
					</div>
				</section>

				<section className="clientes text-white d-flex justify-content-center flex-column mx-5">
					<h4 className="display-4 text-center tituloTabs py-3">Canales</h4>
					<div className="canales d-inline-flex justify-content-center align-items-center mb-5">
						<div className="canal p-3 m-3">
							Cosquín - Logo
						</div>
						<div className="canal p-3 m-3">
							Avicultura - Logo
						</div>
						<div className="canal p-3 m-3">
							Casa Naranja - Logo
						</div>

					</div>
				</section>
			</div>
		);
	}
}

export default Landing;
